import MescrollVue from 'mescroll.js/mescroll.vue'
import "mescroll.js/mescroll.min.css";
import { Indicator } from "mint-ui";
import { selectType } from "../../server/employee";
export default {
  name: "client",
  components:{MescrollVue },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll:null,
       mescrollDown:{ //下拉刷新
        callback:this.downCallBack,
        clearEmptyId:"main",
						isBoth: false, 
						isBounce: true, 
         },
      mescrollUp: { // 上拉加载的配置.
          callback: this.getList,
          htmlNodata: '<p class="upwarp-nodata">  </p>',
          noMoreSize: 5, 
          page: {
            num: 0,
            size: 50,
            total: 0
          },
          empty: {
          warpId: "main", 
          icon: "./static/mescroll/mescroll-empty.png", 
          tip: "暂无相关数据~" //提示
          },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',
      },
      pages: {
        page: 1,
        size: 50,
        total: 0
      },
      dataList: [], // 列表数据
      placeholder:'请输入关键字'
    };
  },
  // 监听input
   watch: {
    value: function (newval,oldval) {
      if (this.value == "") {
        this.placeholder='请输入关键字'
        this.handleSeach()
      }else{
        this.placeholder=''
      }
    }
  },
  methods: {
  //清空input
  handleCommentFocus(){
      this.placeholder=''
    },
    //搜索列表
    search(){
      this.handleSeach()
    },
     handleSeach(){
       let searchList = []
       const that = this
      selectType({
        page: this.pages.page,
        limit: this.pages.size,
        customerName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          this.searchList=e.data.data.list
          this.dataList=this.searchList
        }
      });
    },
    //初始化上拉加载
    mescrollInit (mescroll) {
      this.mescroll = mescroll  
    },
    //下拉刷新的回调
    downCallBack(mescroll){
        setTimeout(function(){
        mescroll.endSuccess()
      },1500);
    },
    getList(page, mescroll) {
    const that = this
      selectType({
        page: page.num,
        limit: page.size,
        customerName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          var arr = e.data.data.list
           let data = page.num == 1 ? [] : this.dataList;
           this.dataList = this.dataList.concat(arr)
           this.$nextTick(() => {
              this.mescroll.endSuccess(arr.length)
            })
        }
      });
    },
    handleLink(id,customerName){
      localStorage.setItem("customerName", JSON.stringify(customerName))
      localStorage.setItem("customerId", JSON.stringify(id))
      this.$router.push({
        path:'/AddEmployee',
        query:{
          customerId:id,
        }
      })
    }
  }
};



